@font-face {
	font-family: Proxima Nova;
	src: url("./fonts/Proxima_Nova_Regular.otf") format('opentype');
}

@font-face {
	font-family: Proxima Nova;
	font-weight: bold;
	src: url("./fonts/Proxima_Nova_Bold.otf") format('opentype');
}

@mixin flexCenter($flexDirection) {
  display: flex;
  flex-direction: $flexDirection;
  justify-content: center;
  align-items: center;
}

.root {
  width: 100vw;
}
body {
  display: flex;
  justify-content: center;
  font-family: Proxima Nova, sans-serif;
}
/* =Fade.js */
@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes fadeOut {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

/* =Home.js */
.home-header, .home-main {
  width: 960px;
}

.home-header {
  height: 324px;
  margin: 0px auto 50px;
  @include flexCenter(column);

  background-image: url("https://images.unsplash.com/photo-1549880338-65ddcdfd017b?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=2250&q=80");
  background-size: cover;
  background-repeat: no-repeat;

  text-align: center;
}

.header-text {
  height: 168px;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;

  h1, h2 {
    font-weight: bold;
  }
  h1 {
    font-size: 40px;
  }
  h2 {
    margin-bottom: 1em;
    font-size: 35px;
  }
