.home-main {
  margin: 0 auto;

  h2 {
    text-align: center;
    font-size: 30px;
    font-weight: bold;
  }
  h3 {
    margin-bottom: 25px;
    font-size: 20px;
    font-weight: bold;
  }
}

footer {
  margin: 20px;
  @include flexCenter(row);
  .link-icon {
    margin: 0 20px;
    font-size: 45px;
    color: black;
  }
}

/* =NavBar.js */
.nav-bar {
  padding: 15px;
  text-align: right;

  a {
    margin-right: 2em;
    font-size: 20px;
  }
}

/* =Project.js */
.project {
  padding: 20px;
  margin-bottom: 50px;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;

  &.reverse {
    flex-direction: row-reverse;
    
  }
}
.info-block, .image-block {
  width: 400px
}
.info-block {
  h2 {
    font-weight: bold;
  }
  p {
    margin-bottom: 20px;
    line-height: 20px;
  }
}
.image-block {
  width: 400px;
  height: auto;
}

